import React from "react";
import { Link, NavLink } from "react-router-dom";
import "../css/Header.css"; // ✅ CSS 파일 연결

const Header = () => {
  return (
    <header className="header">
      <div className="logo">
        <Link to="/">PORTFOLIO</Link> {/* ✅ 로고 클릭 시 메인 페이지 이동 */}
      </div>
      <nav>
        <ul>
          <li>
            <NavLink to="/gallery" activeClassName="active">갤러리</NavLink> {/* ✅ 갤러리 버튼 */}
          </li>
          <li>
            <NavLink to="/product" activeClassName="active">제품소개</NavLink>
          </li>
          <li>
            <NavLink to="/faq" activeClassName="active">FAQ</NavLink>
          </li>
        </ul>
      </nav>
    </header>
  );
};

export default Header;
