import React, { useState } from "react";
import "../css/FAQ.css";

const FAQ = () => {
  const [activeIndex, setActiveIndex] = useState(null);

  const faqs = [
    {
      question: "온라인 스탬프는 어떻게 적립하나요?",
      answer: "온라인 스탬프는 구매한 영수증을 등록하여 적립할 수 있습니다."
    },
    {
      question: "스탬프 적립 후 혜택은 무엇인가요?",
      answer: "스탬프 10개를 적립하면 다양한 사은품을 신청할 수 있습니다."
    },
    {
      question: "스탬프 적립은 어디에서 할 수 있나요?",
      answer: "빌리프 공식 웹사이트의 온라인 스탬프 페이지에서 가능합니다."
    },
    {
      question: "스탬프 적립 기한은 있나요?",
      answer: "구매일로부터 30일 이내에 등록해야 적립이 가능합니다."
    },
  ];

  const toggleAnswer = (index) => {
    setActiveIndex(index === activeIndex ? null : index);
  };

  return (
    <div className="faq">
      <h1>자주 묻는 질문 (FAQ)</h1>
      <ul className="faq-list">
        {faqs.map((faq, index) => (
          <li key={index} className="faq-item">
            <div
              className="faq-question"
              onClick={() => toggleAnswer(index)}
            >
              {faq.question}
            </div>
            {activeIndex === index && (
              <div className="faq-answer">{faq.answer}</div>
            )}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default FAQ;
